'use client';

import sendEmail from '@/utils/sendEmail';
import { useState } from 'react';

export default function ContactForm() {
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [result, setResult] = useState('');

  const handleSubmit = async (formData: FormData) => {
    try {
      await sendEmail(formData);
      setEmail('');
      setMessage('');
      setResult('メールを送信しました');
    } catch (e) {
      console.log(e);
      setResult('送信に失敗しました');
    }
  };

  return (
    <div className="max-w-2xl mx-auto px-6">
      <h2 className="text-2xl sm:text-3xl font-bold text-gray-800 text-center mb-8">
        お問い合わせ
      </h2>
      <p className="text-center text-gray-600 mb-8">
        ご意見やその他ご連絡はこちらからお願いします。
      </p>
      <form action={handleSubmit} className="space-y-6">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">メールアドレス</label>
          <input
            type="email"
            className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-sky-300 focus:border-sky-400"
            placeholder="example@gmail.com"
            required
            value={email}
            onChange={(e) => {
              setEmail(e.target.value);
            }}
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">お問い合わせ内容</label>
          <textarea
            rows={4}
            className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-sky-300 focus:border-sky-400"
            placeholder="ご用件を自由にお書きください"
            required
            value={message}
            onChange={(e) => {
              setMessage(e.target.value);
            }}
          ></textarea>
        </div>

        <div className="text-xs text-center">{result}</div>

        <button className="py-3 bg-sky-400 text-white rounded-lg font-medium hover:bg-sky-500 transition">
          送信する
        </button>
      </form>
    </div>
  );
}
